import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false

new Vue({
  data() {
    return {
      show: true,
      showpass: false,
      email: "",
      password: "",
      select: "General",
      items: ["General", "Corporate"],
      rules: {
        required: (value) => !!value || "",
        min: (v) => v.length >= 8 || "Минимум 8 символов",
        // emailMatch: () =>
        //   "The email and password you entered don't match",
      },
    };
  },
  methods: {
    RegInputVisible() {
      if (this.select == "General") {
        this.show = !this.show;
      } else {
        this.show = !this.show;
      }
    },
  },
  render: h => h(App),
}).$mount('#app')
